button {
    background: #fff;
    border: none;
    border-radius: 4px;
    color: #333;
    cursor: pointer;
    font-size: .9rem;
    padding: 0.5rem 1rem;
    font-family: 'Georama';
    transition: all 0.2s ease-in-out;
    
    &:hover {
        background: #333;
        color: #fff;
    }
}